import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
import IndexPage from '@/components/Index'
import Pages from '@/components/Pages'
import Makeup from '@/components/Makeup'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'indexPage',
      component: IndexPage
    },
    {
      path: '/editorial',
      name: 'editorial',
      component: Pages
    },
    {
      path: '/advertising',
      name: 'advertising',
      component: Pages
    },
    {
      path: '/mens_grooming',
      name: 'mens_grooming',
      component: Pages
    },
    {
      path: '/wedding',
      name: 'wedding',
      component: Pages
    },
    {
      path: '/beauty',
      name: 'beauty',
      component: Pages
    },
    {
      path: '/conceptual_beauty',
      name: 'conceptual_beauty',
      component: Pages
    },
    {
      path: '/makeup',
      name: 'makeup',
      component: Makeup
    }
  ]
})



