<template>
  <div id="app" :class="{'mainOpen': navOC}">
    <div class="mask"></div>
    <header>
      <nav class="flexBox">
        <ul>
            <li class="homePage btn indexNow"><router-link to="/"><img src="./assets/images/icon_house.svg" alt="HomePage"></router-link></li>
            <li class="makeupBtn btn makeup"><router-link to="/makeup">Makeup</router-link></li>
            <li class="ediBtn btn editorial"><router-link to="/editorial">Editorial</router-link></li>
            <li class="adverBtn btn advertising"><router-link to="/advertising">Advertising</router-link></li>
            <li class="groomingBtn btn mens_grooming"><router-link to="/mens_grooming">Men's grooming</router-link></li>
            <li class="weddingBtn btn wedding">
                <router-link to="/wedding">Wedding</router-link>
        <!--
                <ul class="weddingCont">
                    <li><a href="#">Pre-wedding</a></li>
                    <li><a href="#">Wedding day</a></li>
                </ul>
        -->
            </li>
            <!-- <li class="videoBtn btn videos"><router-link to="/videos">Videos</router-link></li>
            <li class="aboutBtn btn aboutme"><router-link to="/aboutme">About Me</router-link></li> -->
        </ul>
        <div class="navLogo absolute">
            <router-link to="/" title="Return To Homepage">
              <img class="showUp" src="./assets/images/mineva_hover.svg" alt="Mineva's Website">
            </router-link>
        </div>
    </nav>
    </header>
    <div class="toggleBtn" :class="{'open': navOC}" v-on:click="navShow"><span></span></div>
<!--*** view block **************************-->
    <transition name="page" mode="out-in">
      <router-view :key="$route.path"/>
    </transition>
<!--*** view block **************************-->
    <footer :class="footerId()">
      <div class="container">
          <div class="copyright">&copy; 2019 Mineva H. All Rights Reserved</div>
      </div>
      <div class="mainLogoBG"></div>
      <div class="mainLogo">
          <router-link to="/">
            <img class="showUp" src="./assets/images/mineva.svg" alt="Mineva's Website">
            <img class="hideUp" src="./assets/images/mineva_hover.svg" alt="Mineva's Website">
          </router-link>
          <h2 class="pageTlt">{{pageTlt()}}</h2>    
      </div>
    </footer>
  </div>
</template>

<script>
export default {
  name: 'App',
  data(){
      return {
          nowPage: '',
          navOC: false
      }
  },
  methods: {
    footerId(){
      let pp = this.$route.path.slice(1)
      if(pp == ''){
        pp='index'
      }
      return pp
    },
    navShow(){
      this.navOC = !this.navOC
    },
    pageTlt(){
      let pageName = this.$route.path.slice(1)
      // return pageName.replace(/\b(\w)(\w*)/g, function($0, $1, $2) {
      //   return $1.toUpperCase() + $2.toLowerCase();
      // });
      return pageName.toLowerCase().replace(/( |^)[a-z]/g, (L) => L.toUpperCase());
      // return pageName
    },
    navActF(){
      $('nav .btn').removeClass('active')
      let pagePath = this.$route.path.slice(1)
      if(pagePath == ''){
        pagePath = 'indexNow'
      }
      $('.' + pagePath + '.btn').addClass('active')
    }
  },
  mounted(){
    this.navActF()
  },
  watch: {
    $route(){
      this.navActF()
      setTimeout(()=>{
        this.navOC = false
      }, 750)
      // $('.loadingBar').fadeIn(0).delay(500).fadeOut(500)
    }
  }
}
</script>

<style>
.page-enter-active, .page-leave-active{
  transition: .5s;
}
.page-enter, .page-leave-to{
  opacity: 0.5;
} 

</style>



