<template>
    <main id="main" class="indexPage">
        <div class="container flexBox">                
            <div class="BigBox">
                <slick class="slickBox" ref="slickIndex" :options="slickOptions">
                    <div v-for="slickI in slickList">
                        <router-link :to="slickI.link" :data-cid="slickI.cid">
                            <img :src="slickI.imgUrl" :alt="slickI.alt" :title="slickI.alt">
                        </router-link>
                    </div>
                </slick>
            </div>                
        </div>
    </main>
</template>

<script>
import Slick from 'vue-slick'
export default {
    data(){
        return{
            slickList:[],
            slickOptions: {
                autoplay: true,
                autoplaySpeed: 3000,
                speed: 500,
                dots: false,
                // arrows: false,
                pauseOnFocus: false,
                adaptiveHeight: true,
                // touchMove: false,
                // swipe: false
            }
        }
    },
    components: {
        'slick': Slick,
    },
    beforeUpdate() {
        if (this.$refs.slickIndex) {
            this.$refs.slickIndex.destroy();
        }
    },
    updated() {
        if (this.$refs.slickIndex && !this.$refs.slickIndex.$el.classList.contains('slick-initialized')) {
            this.$refs.slickIndex.create();
        }
    },
    methods: {
        getSlickList(){
            // this.axios.get('../static/json/index.json').then((res)=>{
            this.axios.get('http://lai-chih.com/Mineva/json/index.json').then((res)=>{
                this.slickList=res.data;
                console.log('data correct');
            }).catch((res)=>{
                console.log('data error');
            })
        },
        // reInit() {
        //     // Helpful if you have to deal with v-for to update dynamic lists
        //     this.$nextTick(() => {
        //         this.$refs.slickIndex.reSlick();
        //     });
        // }
    },
    mounted(){
        this.getSlickList()
    }
}
</script>

<style scoped>
/* main
------------------------------------------------------------------------------*/
.indexPage{
    position: absolute;
    left: 0;
    z-index: 2;
    background-color: #ffffff;    
    width: 100%;
    height: 100%;
    min-height: calc(500px + 1rem);
    overflow: hidden;
    transition: all 0.5s ease;
}
#main .container{
    position: relative;
    justify-content: center;
    align-items: center;
}
.BigBox{
    width: 70%;
    max-width: 1150px;
    height: auto;
}
.slickBox{
    max-height: 100%;
    /* overflow: hidden; */
    box-shadow: 00px 00px 100px rgba(255,255,255,1);
    border: 3px solid rgba(255,255,255,0.7);
    background-color: rgba(255,255,255,0.8);
}

.slickBox img{
    user-select: none;
}
.slickBox a{
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
}

/* media
----------------------------------------------------------------------------*/
/* 各解析度原始單位 */
@media screen and (max-width: 768px) {
    .BigBox{ width: calc(100% - 30px);}
    .index .mainLogo{ width: 3.2rem; top: 0.2rem; left: 0.2rem;}
}
@media screen and (max-width: 585px) {
    .index .mainLogo{ width: 2.4rem;}
}

</style>



